<template>
  <v-stepper v-model="e1">
    <camera-dialog @saveClose="onSaveClose" @cancelClose="onCancelClose"></camera-dialog>

    <v-stepper-header>
      <template v-for="n in steps">
        <v-stepper-step
          :complete="e1 > n"
          :key="`${n}-step`"
          :step="n"
          editable
        >
          Guest No.{{ n }}
        </v-stepper-step>

        <v-divider
          v-if="n !== steps"
          :key="n"
        ></v-divider>
      </template>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content
        v-for="n in steps"
        :key="`${n}-content`"
        :step="n"
      >
      <v-layout row fill-height>
        <v-flex xs6>
          <span class="headline"><v-btn class="headline" flat @click="showDialog(true)">{{ $t("checkIn.titleId") }}</v-btn></span>
        </v-flex>
        <v-flex xs6>
          <span class="headline"><v-btn class="headline" flat @click="showDialog(false)">{{ $t("checkIn.titleFace") }}</v-btn></span>
        </v-flex>
      </v-layout>  
      <v-layout row fill-height>
        <v-flex xs12>
          <v-container fluid>
            <v-layout row justify-space-between>
              <v-flex xs6>
                <img :src="idPresignedUrl" class="img-responsive" width="500px" @click="showDialog(true)" style="cursor: pointer;"/>
              </v-flex>
              <v-flex xs6>
                <img :src="facePresignedUrl" class="img-responsive" width="500px" @click="showDialog(false)" style="cursor: pointer;"/>
              </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
      </v-layout>

      <v-layout row fill-height>
        <v-flex xs12>
          <v-container fluid>
            <v-layout row justify-space-between>
              <v-flex xs3>
                <v-text-field label="*First name" required hint="*" v-model="guest.firstName" v-if="guest" ></v-text-field>
              </v-flex>
              <v-flex xs3>
                <v-text-field label="Middle name"></v-text-field> 
              </v-flex>
              <v-flex xs3>
                <v-text-field label="*Last name" required hint="*" v-model="guest.lastName" v-if="guest" ></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
          <small>*indicates required field</small>
        </v-flex>
      </v-layout>

      <v-layout row fill-height>
        <v-flex xs12>
          <v-spacer></v-spacer>
          <!-- <v-btn color="blue darken-2" flat @click="showDialog(false)">Take Picture</v-btn> -->
          <v-btn class="headline" flat @click="prevStep(n)">{{ $t("checkIn.previous") }}</v-btn>
          <v-btn class="headline" flat @click="verify()">{{ $t("checkIn.verifyId") }}</v-btn>
          <v-btn class="headline" flat @click="nextStep(n)">{{ $t("checkIn.next") }}</v-btn>
        </v-flex>
      </v-layout>  

      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import CameraDialog from '@/components/CameraDialog.vue'
import { EventBus } from '@/eventBus'
import { Storage } from 'aws-amplify'
import aws_exports from '@/aws-exports'
import storage from '@/api/storage'

import { mapState, mapGetters } from 'vuex'

// console.log('CheckInstorage', storage)

export default {
  name: 'CheckIn',
  data () {
    return {
      e1: 100,
      imageSaved: 0
    }
  },
  created () {
    this.$store.dispatch('checkIn/loadGuests', this.$store.state.amplify.user.username)
  },
  watch: {
    steps (val) {
      console.log('watch steps:', val)
      console.log('this.e1:', this.e1)
      if (this.e1 > val) {
        this.e1 = 1
      }
    }
  },
  computed: {
    guest: {
      get() {
        return this.guests[this.e1 - 1]
      },
    },
    ...mapState({
      userIdentityId: state => state.amplify.userIdentityId,
      guests: state => state.checkIn.guests
    }),
    ...mapGetters('checkIn', {
      steps: 'steps'
    })
  },
  asyncComputed: {
    idPresignedUrl: {
      get() {
        if (this.guest) {
          if (this.guest.idImgName) {
            return storage.get(this.guest.idImgName)
          }
        }

        return require('../assets/img/id.svg')
      },
      watch () {
        this.imageSaved
      }
    },
    facePresignedUrl: {
      get() {
        if (this.guest) {
          if (this.guest.faceImgName) {
            const rtn = storage.get(this.guest.faceImgName)
            return rtn
          }
        } 

        return require('../assets/img/face.svg')   
      },
      watch () {
        this.imageSaved
      }
    }
  },
  methods: {
    prevStep (n) {
      if (n === 1) {
        this.e1 = this.steps
      } else {
        this.e1 = n - 1
      }
    },
    nextStep (n) {
      if (n === this.steps) {
        this.e1 = 1
      } else {
        this.e1 = n + 1
      }
    },
    verify() {
      // console.log('guest', this.guest)
      this.$store.dispatch('checkIn/saveGuest', this.guest)
    },
    showDialog(isId) {
      EventBus.$emit('dialog', { showDialog: true, isId });
      this.isId = isId;
    },
    onCancelClose() {
      console.log('cancelClose')
    },
    async onSaveClose(val) {
      console.log('saveClose')

      if (val.base64Img) {
        if (val.isId) {
          this.guest.idBase64Img = val.base64Img
          await this.$store.dispatch('checkIn/putImage', {guest: this.guest, isId: val.isId})
        } else {
          this.guest.faceBase64Img = val.base64Img
          await this.$store.dispatch('checkIn/putImage', {guest: this.guest, isId: val.isId})
        }
      }

      this.imageSaved++
      
    },
    // async saveImage(val) {
    //   if (val.base64Img) {
    //     if (val.isId) {
    //       await storage.put(this.guest.idImgName, val.base64Img)
    //     } else {
    //       await storage.put(this.guest.faceImgName, val.base64Img)
    //     }
    //   }
    // },
    // async onSaveClose(val) {
    //   console.log('saveClose')
      
    //   await this.saveImage(val)
    //   this.imageSaved++
    // },
  },
  components: {
    cameraDialog: CameraDialog
  }
}
</script>